import React from 'react';
import ReactDOM from 'react-dom';
import MainHeader from './MainHeader';
import './style.css';

let jsContainer = document.getElementById('js');

const render = () => {
  jsContainer.innerHTML = `
  <div class="demo">
    Hello JS
    <input />
    <p>${new Date()}</p>
  </div>
`;

  let reactContainer = document.getElementById('react');
  ReactDOM.render(
    React.createElement(
      "div", { className: "demo" },
      "Hello ReactING",
      React.createElement("input"),
      React.createElement(
        "p",
        null,
        new Date().toString()
      )
    ),
    reactContainer
  )


  ReactDOM.render(<MainHeader />, document.getElementById('main-header'));
  
}

setInterval(render, 1000);